import React,{useState} from 'react';
import TextTransition, { presets } from "react-text-transition";
const TEXTS = [
    "Abrorbek Ma'rufov",
    "Frontend developer",
    "Javascript developer",
    "React developer"
];

const Main = () => {
    const [index, setIndex] =useState(0);
    React.useEffect(() => {
        const intervalId = setInterval(() =>
                setIndex(index => index + 1),
            2000 // every 3 seconds
        );
        return () => clearTimeout(intervalId);
    }, []);
    return (
        <>
            <section className="main">
                <div className="container">
                    <div className="row">
                        <div className="col-6">
                            <h2>Hi I am </h2>
                            <h3>
                                <TextTransition
                                    text={ TEXTS[index % TEXTS.length] }
                                    springConfig={ presets.gentle }
                                />
                            </h3>
                            <h5>
                                <i className="fas fa-chevron-left"/>
                                coder
                                <i className="fas fa-chevron-right"/>
                            </h5>
                            <p>Front end developer who writes <br/> clean, elegant and efficient code.</p>
                        </div>
                        <div className="col-6">
                            <figure><img  src="abror.jpg" alt=""/></figure>
                        </div>
                        <div className="col-8 offset-2">
                            <a className="text-decoration-none" target="_blank" href="https://t.me/AbrorPortfolio"><button className="btn">Resume</button></a>
                        </div>
                    </div>
                </div>
            </section>
        </>
    );
};

export default Main;