import React from 'react';
import { Progress } from 'reactstrap';

const About = () => {



    return (
        <>
            <section className="about">
                <div className="container">
                    <div className="row">
                        <div className="col-6">
                            <h1>about.</h1>
                            <h6>I am clever,creative and junior <br/> Frontend Developer from Uzbekistan</h6>
                            <p>I am Abrorbek Ma'rufov.I was born on August 15, 2002 in Tashkent.
                                I have been interested in English and mathematics since I was young. I have been
                                involved in programming for almost a year. I am a winner of the Physics Science Regional
                                Olympiad. I studied Frontend at the PDP IT Academy.</p>
                        </div>
                        <div className="col-6">
                            <figure>
                                <img src="abror.jpg" alt=""/>
                            </figure>
                        </div>
                    </div>
                    <div className="skills">
                        <div>
                            <Progress color="dark" value={100}><div className="d-flex justify-content-between align-items-center"><h6>HTML5</h6> <h6>100%</h6></div></Progress>
                            <Progress color="dark" value={100}><div className="d-flex justify-content-between align-items-center"><h6>CSS3</h6> <h6>100%</h6></div></Progress>
                            <Progress color="dark" value={100}><div className="d-flex justify-content-between align-items-center"><h6>SASS</h6> <h6>100%</h6></div></Progress>
                            <Progress value={100} color="dark"><div className="d-flex justify-content-between align-items-center"><h6>Bootstrap4</h6> <h6>100%</h6></div></Progress>
                            <Progress value={90} color="dark"><div className="d-flex justify-content-between align-items-center"><h6>Material UI</h6> <h6>90%</h6></div></Progress>
                            <Progress value={80} color="dark"><div className="d-flex justify-content-between align-items-center"><h6>Javascript</h6> <h6>80%</h6></div></Progress>
                            <Progress value={80} color="dark"><div className="d-flex justify-content-between align-items-center"><h6>React Js</h6> <h6>80%</h6></div></Progress>
                        </div>
                    </div>
                </div>
            </section>
        </>
    );
};

export default About;