import React from 'react';
import {Col, Form, FormGroup, Input, Label} from "reactstrap";

const Contact = () => {
    return (
        <>
            <section className="contact">
                <div className="container">
                    <div className="row first">
                        <div className="col-6">
                            <h1>contact.</h1>
                            <h6>Do you speak Uzbek? It's ok if you don't, <br/> I speak English too.</h6>

                        </div>
                        <div className="col-6">
                            <figure>
                                <img
                                    src="https://c1.wallpaperflare.com/preview/987/457/165/contact-us-contact-call-us-phone-email.jpg"
                                    alt=""/>
                            </figure>
                        </div>
                    </div>
                    <div className="row">

                        <div className="col-6">
                            <Form>
                                <FormGroup>
                                    <Label for="exampleEmail">Name</Label>
                                    <Input type="text" name="name" id="exampleEmail"  />
                                </FormGroup>
                                <FormGroup>
                                    <Label for="exampleEmail">Email</Label>
                                    <Input type="email" name="email" id="exampleEmail" />
                                </FormGroup>
                            </Form>
                        </div>
                        <div className="col-6">
                            <FormGroup >
                                <Label for="exampleText">Message</Label>
                                <Input  type="textarea" name="text" id="exampleText" />
                            </FormGroup>
                        </div>
                        <div className="col-8 offset-2">
                            <button className="btn btn-dark w-100 mt-5" type="submit">Send</button>
                        </div>
                    </div>
                </div>
            </section>
        </>
    );
};

export default Contact;