import React,{useState} from 'react';
import {Link} from "react-router-dom";

const Menu = () => {
    const white="#fff";
    const gray="#757575"
    const [style,setStyle]=useState(white)
    const [styleSec,setStyleSec]=useState(white)
    const [styleThree,setStyleThree]=useState(white)
    const [styleFour,setStyleFour]=useState(white)
    const [none,setNone]=useState('none')
    return (
        <>
            <section  className="menu">
                <div className="container">
                    <div  className="top">
                        <div className="left">
                           <Link to="/">
                               <div className="brand">
                                   <h3>A</h3>
                               </div>
                           </Link>
                        </div>
                        <div onClick={()=>{
                            if (none==='none'){
                                setNone('block')
                            }else if (none==='block'){
                                setNone("none")
                            }
                        }} className="btn hamburg"><i className="fas fa-bars"/></div>
                        <div style={{display:none}} className="d-nonee">
                            <div className="links">
                                <Link onClick={()=>{
                                    setStyle(gray)
                                    setStyleSec(white)
                                    setStyleThree(white)
                                    setStyleFour(white)
                                    setNone('none')
                                }} style={{color:style}} to="/">home</Link>
                                <Link onClick={()=>{
                                    setStyleSec(gray)
                                    setStyle(white)
                                    setStyleThree(white)
                                    setStyleFour(white)
                                    setNone('none')
                                }} style={{color:styleSec}} to="/about">about</Link>
                                <Link onClick={()=>{
                                    setStyleThree(gray)
                                    setStyleSec(white)
                                    setStyle(white)
                                    setStyleFour(white)
                                    setNone('none')
                                }} style={{color:styleThree}} to="/portfolio">portfolio</Link>
                                <Link onClick={()=>{
                                    setStyleFour(gray)
                                    setStyleThree(white)
                                    setStyleSec(white)
                                    setStyle(white)
                                    setNone('none')
                                }} style={{color:styleFour}} to="/contact">contact</Link>
                            </div>
                            <div className="social-links two">
                                <a onClick={()=>{
                                    setNone('none')
                                }} className="text-decoration-none" target="_blank" href="https://t.me/abror_muzaffarovic"><i className="fab fa-telegram-plane"/></a>
                                <a  onClick={()=>{
                                    setNone('none')
                                }}  className="text-decoration-none" target="_blank" href="https://www.instagram.com/abror__muzaffarovich__/"><i className="fab fa-instagram"/></a>
                                <a  onClick={()=>{
                                    setNone('none')
                                }} className="text-decoration-none" target="_blank" href="https://github.com/Abror789"> <i className="fab fa-github"/></a>
                            </div>
                        </div>
                        <div className="right">
                            <div className="menu-link">
                                <Link onClick={()=>{
                                    setStyle(gray)
                                    setStyleSec(white)
                                    setStyleThree(white)
                                    setStyleFour(white)
                                }} style={{color:style}} to="/">home</Link>
                                <Link onClick={()=>{
                                    setStyleSec(gray)
                                    setStyle(white)
                                    setStyleThree(white)
                                    setStyleFour(white)
                                }} style={{color:styleSec}} to="/about">about</Link>
                                <Link onClick={()=>{
                                    setStyleThree(gray)
                                    setStyleSec(white)
                                    setStyle(white)
                                    setStyleFour(white)
                                }} style={{color:styleThree}} to="/portfolio">portfolio</Link>
                                <Link onClick={()=>{
                                    setStyleFour(gray)
                                    setStyleThree(white)
                                    setStyleSec(white)
                                    setStyle(white)
                                }} style={{color:styleFour}} to="/contact">contact</Link>
                            </div>

                            <div className="social-links">
                                <a className="text-decoration-none" target="_blank" href="https://t.me/abror_muzaffarovic"><i className="fab fa-telegram-plane"/></a>
                                <a  className="text-decoration-none" target="_blank" href="https://www.instagram.com/abror__muzaffarovich__/"><i className="fab fa-instagram"/></a>
                                <a className="text-decoration-none" target="_blank" href="https://github.com/Abror789"> <i className="fab fa-github"/></a>
                            </div>

                        </div>
                    </div>
                </div>
            </section>
        </>
    );
};

export default Menu;